.MyModal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0,0,0,0.5);
    z-index: 1000;
    color: black;
}

.MyModalContent{
    padding: 25px;
    background: white;
    border-radius: 18px;
    min-width: 250px;
}


.MyModal.active{
    display: flex;
    justify-content: center;
    align-items: center;
}